.form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  width: 100%;
  max-height: 250px;
  overflow-y: auto;
  margin-bottom: 20px;
}

/* Скрываем scrollbar для Chrome, Safari и Opera */
.form::-webkit-scrollbar {
  display: none;
}
 
/* Скрываем scrollbar для IE, Edge и Firefox */
.form {
  -ms-overflow-style: none;
  scrollbar-width: none;
}