.side {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;

  width: fit-content;

  background-color: #3f4048;
}

.container {
  display: flex;
  flex-direction: row-reverse;
  min-width: 470px;
  height: 100%;
  width: 100%;
}

.content {
  position: relative;
  
  display: flex;
  flex-direction: column;
  width: 100%;
}

.header {
  padding: 20px;

  font-family: 'Open Sans', sans-serif;
  color: #cbcbcb;
  text-align: center;
}