.button {
  flex-shrink: 0;
  border: 0;
  width: fit-content;
  height: fit-content;

  font-family: 'Open Sans', sans-serif;

  cursor: pointer;
}

.small {
  padding: 5px;
  border-radius: 5px;

  font-size: 16px;
}

.medium {
  padding: 10px 20px;
  border-radius: 10px;

  font-size: 18px;
}

.blue {
  background-color: #5a89f6;
}

.blue:hover,
.blue:focus {
  background-color: #4774de
}

.green {
  background-color: #4ab540;
}

.green:hover,
.green:focus {
  background-color: #379e2e;
}

.red {
  background-color: #e1564f;
}

.red:hover, 
.red:focus {
  background-color: #da4038;
}

.dark {
  background-color: #50515b;
}

.dark:hover,
.dark:focus {
  background-color: #27292f;
}

.font-white {
  color: #ffffff;
}

.font-blue {
  color: #5a89f6
}


.font-green {
  color: #5b9152;
}
