.button {
  position: relative;

  flex-shrink: 0;
  width: 40px;
  height: 40px;
  border: 0;
  border-radius: 10px;

  background-color: #282a30;
  cursor: pointer;
}

.button::before,
.button::after {
  position: absolute;
  top: 50%;
  left: 50%;

  content: '';
  width: 20px;
  height: 4px;
  border-radius: 2px;

  background-color: #cbcbcb;
}

.button::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.button::after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.button:hover::before,
.button:hover::after,
.button:focus::before,
.button:focus::after {
  background-color: #da4038
}