.container {
  position: relative;
  z-index: 0;

  height: 100%;
  width: 25px;

  background-color: rgba(255, 255, 255, 0);
}

.container:has(button:hover, button:focus) {
  background-color: rgba(255, 255, 255, 0.1);
}

.container::before {
  position: absolute;
  z-index: 1;

  top: 50%;
  left: 50%;

  content: '';
  width: 8px;
  height: 100px;
  border-radius: 5px;
  background-color: #cbcbcb;

  transform: translate(-50%, -50%);
}

.button {
  position: relative;
  z-index: 2;

  width: 25px;
  height: 100%;
  border: 0;

  background-color: transparent;
  cursor: ew-resize;
}

