.fieldset {
  display: flex;
  column-gap: 10px;
  padding: 0;
  padding-left: 50px;
  border: 0;
}

.first {
  padding-left: 0;
}

.buttonContainer {
  position: relative;

  flex-shrink: 0;
}

.buttonContainer::before {
  position: absolute;
  top: 45px;
  left: 50%;
  bottom: 4px;

  content: '';
  width: 0;
  border: 1px dashed ;

  transform: translateX(-50%);
}

.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  row-gap: 10px;
}