.modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.7);
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  height: fit-content;
  max-height: 80%;
  padding: 20px;
  border-radius: 10px;

  background-color: #3f4048;
}

.message {
  width: 100%;
  padding: 10px;
  border: 0;
  border-radius: 10px;
  margin-bottom: 20px;
  overflow-y: auto;
  height: fit-content;
  min-height: 100px;

  font-family: 'Open Sans', sans-serif;
  color: #bcbcbd;
  font-size: 18px;
  overflow-wrap: break-word;
  white-space: pre-wrap;

  background-color: #50515b;
}

/* Скрываем scrollbar для Chrome, Safari и Opera */
.message::-webkit-scrollbar {
  display: none;
}

/* Скрываем scrollbar для IE, Edge и Firefox */
.message {
  -ms-overflow-style: none;
  scrollbar-width: none;
}