.button {
  position: fixed;
  top: 25px;
  left: 25px;

  padding: 10px;
  border: 0;
  border-radius: 10px;
  width: 100px;

  font-family: 'Open Sans', sans-serif;
  color: #ffffff;
  text-align: center;

  background-color: #5a89f6;
  cursor: pointer;
}

.button:hover,
.button:focus {
  background-color: #4774de
}