.container {
  display: flex;
}

.label {
  flex-shrink: 0;
  width: 50px;
  padding-top: 10px;
  padding-right: 10px;

  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  text-align: end;
  color: white;
}

.blue {
  color: #77b4e7;
}

.purple {
  color: #b042a6;
}

.green {
  color: #2be22b;
}

.textarea {
  width: 100%;
  padding: 10px;
  border: 0;
  border-radius: 10px;
  resize: none;
  overflow: hidden;

  font-family: 'Open Sans', sans-serif;
  color: #cbcbcb;

  background-color: #282a30;
}

